:root{
    --primarycolor: hsl(199, 100%, 33%);
    --secondarycolor: hsl(187, 85%, 43%);
    --gradientcolor: linear-gradient(to right, hsl(187, 85%, 43%),hsl(199, 100%, 33%));
    --whitecolor:hsl(0, 0%, 100%);
    --blackcolor:hsl(201, 33%, 16%);
    --textcolor:hsl(240, 4%, 36%);
    --whitecolorDeam:hsl(0, 0%, 93%);
    --graytext:rgb(190, 190, 190);
    --inputcolor:rgb(239, 239, 239);
    --bodycolor: rgb(240, 240, 246);
    --cardBG: rgb(225, 225, 235);
    --cardHover:rgb(225, 225, 225);
}

.footer{
    width: 100%;
    position: relative;
    padding: 2rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;

    .videoDiv{
        position: absolute;
        height: 100%;
        width: 100%;

        video{
            height: 100%;
            object-fit: cover;
        }

        &::after{
            content: '';
            position: absolute;
            background: rgba(10, 84, 107, 0.307);
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            mix-blend-mode: multiply;
        }
    }

    .secContent{
        width: 100%;
        height: max-content;
        padding: 2rem initial;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin: auto;
        row-gap: 2rem;
        z-index: 100;

        .contactDiv{
            justify-content: space-between;
            flex-direction: column;
            gap: 1rem;

            .text{
                small{
                    font-size: 13px;
                    font-weight: 400;
                    color: var(--whitecolor);
                }

                h2{
                  color: var(--whitecolor);
                  font-size: 1.8rem;
                }
            }

            .inputDiv{
                 width: 100%;
                 gap: 1rem;
                 flex-direction: column;

                 input{
                    width: 100%;
                    padding:  .5rem 6rem;
                    border-radius: 3rem;
                    border: 1px solid var(--whitecolor);
                    outline: none;
                    background: transparent;
                    color: var(--whitecolor);


                    &::placeholder{
                        color: var(--whitecolor);
                        opacity: .5;
                    }
                 }

                 .btn{
                    width: 100%;
                    justify-content: center;
                    color: var(--whitecolor);
                    font-weight: 500;
                    gap: .5rem;

                    .icon{
                        font-size: 18px;
                    }
                 }
            }
        }

        .footerCard{
            position: relative;
            padding: 1rem 1rem 4rem;
            gap: 2rem;
            border-radius: 1rem;
            background: var(--bodycolor);
            width: 100%;
            justify-content: center;
            flex-direction: column;
            align-items: center;
            overflow: hidden;

            .footerIntro{
                flex-basis: 50%;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                row-gap: .5rem;
            }

            .logoDiv{
                .logo{
                    color: var(--blackcolor);
                    font-weight: 600;

                    .icon{
                        font-size: 25px;
                        color: var(--primarycolor);
                        margin-right: 10px;
                    }
                }
            }

            .footerparagraph{
                font-size: 13px;
                color: var(--textcolor);
            }

            .footerSocials{
              column-gap: .5rem;

              .icon{
                color: var(--blackcolor);
                font-size: 20px;

                &:hover{
                    color: var(--primarycolor);
                }
              }
            }

            .footerLinks{
                flex-basis: 50%;
                width: 100%;
                gap: 1rem;
                grid-template-columns: repeat(2, 1fr);
                justify-content: space-between;

                .linkGroup{
                    .groupTitle{
                        display: block;
                        font-weight: 600;
                        color: var(--blackcolor);
                        margin-bottom: .5rem;
                    }

                    .footerList{
                        font-size: 15px;
                        color: var(--textcolor);
                        transition: .3s ease-in-out;

                        .icon{
                            font-size: 15px;
                            color: var(--graytext);
                        }

                        &:hover{
                            color: var(--primarycolor);
                            transform: translateX(7px);

                            .icon{
                                color: var(--secondarycolor);
                            }
                        }
                    }
                }
            }

            .footerDiv{
                flex-direction: column;
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                background: var(--secondarycolor);
                padding: .5rem 2rem;


                .small{
                    font-size: 10px;
                    color: var(--whitecolor);
                }
            }
        }
    }
}

@media screen and (min-width: 500px) {
    .footer{
        .secContent{
            .footerCard{
                .footerLinks{
                    grid-template-columns: repeat(3,1fr);
                    padding-bottom: 1rem;
                }

                .footerDiv{
                    justify-content: space-between;
                    flex-direction: row;

                    small{
                        font-size: 14px;
                        color: var(--whitecolor);
                    }
                }
            }
        }
    }
}


@media screen and (min-width: 660px) {
    .footer{
        .secContent{
           .contactDiv{
            flex-direction: row;
            justify-content: space-between;

            .inputDiv{
                width: max-content;
                flex-direction: row;
                justify-content: flex-start;

                input{
                    width: 60%;
                }

                .btn{
                    width: max-content;
                }
            }
           }
        }
    }
}

@media screen and (min-width: 840px) {
    .footer{
        padding: 5rem 0;
        .secContent{
          .footerCard{
            flex-direction: row;

            .footerLinks{
                grid-template-columns: repeat(3,1fr);
                padding-bottom: 1rem ;
            }
          }
        }
    }
}

@media screen and (min-width: 1024px) {
    .footer{
     .secContent{
        .footerCard{
            padding: 3rem 1rem;

            .footerIntro{
                .footerparagraph{
                    font-size: 15px;
                    color: var(--textcolor);
                }

                .footerSocials{
                    column-gap: .5rem;
                    .icon{
                        color: var(--textcolor);
                        font-size: 25px;
                        &::hover{
                            color: var(--primarycolor);
                        }
                    }
                }
            }

            .footerLinks{
                .linkGroup{
                    .footerList{
                        font-size: 16px;
                        padding: 5px 0;
                    }
                }
            }
        }
     }
    }
}